import React, { useEffect, useState } from 'react';
import './App.css';
import SearchBar from './SearchBar';
import Results from './Results';
import Nominations from './Nominations';
import Loading from './Loading';

function App() {

  const API_KEY = 'd797e60'
  // const API_URL = `http://www.omdbapi.com/?s=${term}&apikey=${API_KEY}`;

  const [, setMovies] = useState([]);
  const [, setError] = useState([]);
  const [loading, setLoading] = useState(false);
  // const [nominations, setNominations] = useState(false);
  const [term, setTerm] = useState('spider')

  useEffect(() => {
    
    setLoading(true);
    setError(null);
    setMovies(null);

    fetch(`http://www.omdbapi.com/?s=${term}&apikey=${API_KEY}`)
    .then(res => res)
    .then(res => res.json())
    .then(response => {
        if (response.Response === 'False') {
            setError(response.Error);
        }
        else {
            setMovies(response.Search);
            console.log(response.Search);
        }

        setLoading(false);
    })
    .catch(({message}) => {
        setError(message);
        setLoading(false);
    })
}, [term]);


  return (
    <div className="App">
      <h1>The Shoppies</h1>
      <SearchBar handleSearch={setTerm}/>
      <div id="container">
        <Results />
        <Nominations />
      </div>
      {loading && 
        <Loading />}
      {/* {
        movies.map((m, id) => (
        ))
      } */}
    </div>
  );
}

export default App;
